import React, { useState, useEffect } from 'react';
import './App.css';
import Home from './components/home';
import Services from './components/services'
import Projects from './components/projects'
import About from './components/about'
import Contacts from './components/contacts'
import { Preloader, Placeholder } from 'react-preloading-screen';
import logo from './assets/logo_vertical.png'

function App() {
  var slideIndex = 1;

  useEffect(() => {
    showSlides(slideIndex);//initial display
  });


  function plusSlides(n) {
    showSlides(slideIndex += n);
  }

  function currentSlide(n) {
    showSlides(slideIndex = n);
  }

  function showSlides(n) {
    var i;
    var slides = document.getElementsByClassName("mySlides");
    var dots = document.getElementsByClassName("demo");
    var dot = document.getElementsByClassName("dem");

    if (n > 5) { slideIndex = 1 }
    if (n < 1) { slideIndex = 5 }

    for (i = 0; i < slides.length; i++) {
      slides[i].style.display = "none";

      dots[i].className = dots[i].className.replace(" active", "");
      dot[i].className = dot[i].className.replace(" active", "");

    }
    slides[slideIndex - 1].style.display = "block";

    dots[slideIndex - 1].className += " active";
    dot[slideIndex - 1].className += " active";

  }
  return (
    <Preloader fadeDuration={1300}>
      <div className="App" style={{ position: "relative" }}>


        <div class="slideshow-container">

          <div class="mySlides fade">
            <Home></Home>
            <a class="prev" onClick={(e) => plusSlides(-1)}><span uk-icon="icon: triangle-left ; ratio: 2"></span></a>
            <div class="txt2 uk-visible@l"  >Contact </div>
            <a class="next" onClick={(e) => plusSlides(1)}><span uk-icon="icon: triangle-right ; ratio: 2"></span></a>
            <div class="txt uk-visible@l "  >Services </div>
          </div>

          <div class="mySlides fade">
            <Services></Services>
            <a class="prev" onClick={(e) => plusSlides(-1)}><span uk-icon="icon: triangle-left ; ratio: 2"></span></a>
            <div class="txt2 uk-visible@l"  >Home </div>
            <a class="next" onClick={(e) => plusSlides(1)}><span uk-icon="icon: triangle-right ; ratio: 2"></span></a>
            <div className="txt uk-visible@l">Projects</div>
          </div>

          <div class="mySlides fade">
            <Projects></Projects>
            <a class="prev" onClick={(e) => plusSlides(-1)}><span uk-icon="icon: triangle-left ; ratio: 2"></span></a>
            <div class="txt2 uk-visible@l"  >Services </div>
            <a class="next" onClick={(e) => plusSlides(1)}><span uk-icon="icon: triangle-right ; ratio: 2"></span></a>
            <div class="txt uk-visible@l"  >About </div>
          </div>

          <div class="mySlides fade">
            <About></About>
            <a class="prev" onClick={(e) => plusSlides(-1)}><span uk-icon="icon: triangle-left ; ratio: 2"></span></a>
            <div class="txt2 uk-visible@l"  >Projects </div>
            <a class="next" onClick={(e) => plusSlides(1)}><span uk-icon="icon: triangle-right ; ratio: 2"></span></a>
            <div class="txt uk-visible@l">Contact </div>
          </div>
          <div class="mySlides fade">
            <Contacts></Contacts>
            <a class="prev" onClick={(e) => plusSlides(-1)}><span uk-icon="icon: triangle-left ; ratio: 2"></span></a>
            <div class="txt2 uk-visible@l"  >About </div>
            <a class="next" onClick={(e) => plusSlides(1)}><span uk-icon="icon: triangle-right ; ratio: 2"></span></a>
            <div class="txt uk-visible@l">Home </div>
          </div>
        </div>
        <div class="topnav "  >
          <nav class=" uk-navbar-container" uk-navbar="true">
            <div class="uk-navbar-center">

              <ul class="uk-navbar-nav">
                <li ><a><a class="demo active" href="#home" onClick={(e) => currentSlide(1)}>Home
        <div class="dem act" style={{ marginLeft: "15px" }}></div></a></a></li>

                <li><a href="#"> <a class="demo" href="#news" onClick={(e) => currentSlide(2)}>Services
        <div class="dem " style={{ marginLeft: "21px" }}></div></a></a></li>

                <li><a href="#"><a class="demo" href="#contact" onClick={(e) => currentSlide(3)}>projects
        <div class="dem " style={{ marginLeft: "24px" }}></div></a></a></li>

                <li><a href="#"><a class="demo" href="#about" onClick={(e) => currentSlide(4)}>About
        <div class="dem " style={{ marginLeft: "17px" }}></div></a></a></li>

                <li><a href="#"><a class="demo" href="#about" onClick={(e) => currentSlide(5)}>Contacts
        <div class="dem " style={{ marginLeft: "26px" }}></div></a></a></li>
              </ul>

            </div>
          </nav>
        </div>

      </div>
      <Placeholder>
        <div class="uk-text-center" style={{ backgroundColor: "#0A1623",height:"100vh",width:"100vw" }}> 
         <img width="280px" height="280px" style={{ marginTop: "15%",border:"1px solid #E99639",borderRadius:"50%",padding:"40px" }} src={logo} uk-img="true"></img>
       
        </div>
      </Placeholder>
    </Preloader>
  );
}

export default App;
