import React from 'react'
import './home.css'
import logo from '../assets/logo_vertical.png'
import logotxt from '../assets/logo_text.png'


export default function Home() {
    return (
        <div>
            <div class="uk-hidden@m" style={{ height: "100vh", width: "100vw", backgroundColor: "#0A1623" }}>
                <div class="uk-text-center"style={{ width: "100%", height: "45%", backgroundColor: "#0A1623", borderBottom: "2px solid #323C47" }}>
                    <img width= "140px" height="140px" style={{  marginTop: "20px" }} src={logo} uk-img="true"></img><br></br>
                    <img style={{ width: "160px", height: "35px", marginLeft: "-5px" }} src={logotxt} uk-image="true"></img>

                </div>
                <div class="uk-text-center" style={{ width: "100%", height: "45%", backgroundColor: "#0A1623" }}>
                    <h2 style={{ marginTop: "50px", color: "white", fontWeight: "bolder" }}>Designs that<br></br>makes a difference<br></br></h2>
                    <button class="showreel" style={{  marginTop: "10px", borderRadius: "100px", color: "white", fontSize: "14px", fontWeight: "lighter", opacity: ".7", cursor: "pointer", border: "2px solid #323C47", padding: "10px 20px 10px 20px", backgroundColor: "transparent" }} ><a style={{ color: "#F1A002", marginRight: "7px", marginTop: "-5px" }} uk-icon="play-circle"></a>Play Showreel</button>
                </div>

            </div>

            <div class=" uk-visible@m  uk-child-width-1-2@s" style={{ height: "100vh", width: "100vw", display: "flex", backgroundColor: "#0A1623" }}>
                <div style={{ height: "250px", backgroundColor: "#0A1623", marginTop: '12%',paddingLeft:"25%",borderRight: "2px solid #323C47" }}>
                    <img style={{ width: "170px", height: "170px", marginTop: "25px" }} src={logo} uk-image="true"></img><br></br>
                    <img style={{ width: "190px", height: "40px", marginLeft: "-5px" }} src={logotxt} uk-image="true"></img>

            
                </div>
                <div style={{ height: "250px", backgroundColor: "#0A1623", marginTop: '12%' }}>
                    <h2 style={{ marginTop: "50px", color: "white", marginLeft: "100px", fontWeight: "bolder" }}>Designs that<br></br>makes a difference<br></br></h2>
                    <button class="showreel" style={{ marginLeft: "100px", marginTop: "10px", borderRadius: "100px", color: "white", fontSize: "14px", fontWeight: "lighter", opacity: ".7", cursor: "pointer", border: "2px solid #323C47", padding: "10px 20px 10px 20px", backgroundColor: "transparent" }} ><a style={{ color: "#F1A002", marginRight: "7px", marginTop: "-5px" }} uk-icon="play-circle"></a>Play Showreel</button>
                </div>

            </div>
        </div>
    )
}

